<template>
  <div>
    <div>选中项：{{checked}}</div>
    <tree-node :node="data" :checked="checked" @change="handleChange"></tree-node>
  </div>
</template>

<script>
import TreeNode from './component/tree.vue'
export default {
  name: 'TreeMode',
  components: {
    TreeNode
  },
  data () {
    return {
      data: [],
      checked: []
    }
  },
  created() {
    const cre = (key, index) => {
      let arr = []
      for (let i = 0; i < 3; i++) {
        const keys = key !== '' ? `${key}-${i}` : `${i}`
        arr.push({
          key: keys,
          name: keys,
          children: index < 6 ? cre(keys, ++index) : null
        })
      }
      return arr
    }
    this.data = cre('', 0)
  },
  methods: {
    handleChange(item) {
      this.deal(item)
    },
    deal(item) {
      if (this.checked.includes(item.key)) {
        const index = this.checked.indexOf(item.key)
        this.checked.splice(index, 1)
      } else {
        this.checked.push(item.key)
      }
      this.checked = this.checked.filter(ite => {
        const reg = new RegExp(`^${ite}`, 'g').test(item.key)
        const reg2 = new RegExp(`^${item.key}`, 'g').test(ite)
        return !(reg || reg2) || item.key === ite || (!reg && !reg2)
      })
      // let arr = item.key.split('-')
      // if (arr.length > 1) {
      //   arr.splice(arr.length - 1, 1) // 得到没有最后一项的数组
      //   let newKey = []
      //   arr.forEach((item, index) => {
      //     newKey.push(arr.slice(0, index+1).join('-'))
      //   })
      //   this.checked = this.checked.filter(ite => {
      //     return !newKey.includes(ite) && (!ite.startsWith(item.key) || ite === item.key)
      //   })
      // }
      // if (this.checked.includes(item.key)) {
      //   const index = this.checked.indexOf(item.key)
      //   this.checked.splice(index, 1)
      // } else {
      //   this.checked.push(item.key)
      // }
    }
  }
}
</script>

