<template>
  <div class="tree-node">
    <div v-for="(item, index) in node" :key="index">
      <span @click="handleClick(item)">{{ item.name }}</span>
      <input type="checkbox" :checked="checked.includes(item.key)" disabled />
      <Tree v-if="item.children" :node="item.children" :checked="checked" @change="handleClick"></Tree>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Tree',
  props: {
    checked: {
      type: Array,
      default: () => []
    },
    node: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    handleClick(item) {
      this.$emit('change', item)
    }
  }
}
</script>

<style scoped>
.tree-node{
  padding-left:10px;
}
span{
  cursor: pointer;
  user-select: none;
}
span:hover{
  background:red;
  color:#fff;
}
</style>
